@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{background:#000;font-family:'Montserrat', sans-serif}header{color:white;display:flex;justify-content:center}header h1{font-size:52px;font-weight:500}

.evidence-box{background-color:rgba(61,61,61,0.411);color:white;width:700px;margin:auto;border:white 1px solid;display:flex;justify-content:center;align-items:center;font-weight:300;font-size:14px}.evidence-box .evidence{width:100px;height:75px;display:flex;justify-content:center;align-items:center}.evidence-box .evidence .evidence-text{-ms-user-select:none;-moz-user-select:none;-khtml-user-select:none;-webkit-user-select:none}.evidence-box .confirmed{background-color:#003e00}.evidence-box .declined{background-color:#770303}.evidence-box .blocked{background-color:darkred}.evidence-box .evidence.blocked:hover{background-color:darkred}.evidence-box .evidence.confirmed:hover{background-color:#003e00}.evidence-box .evidence.declined:hover{background-color:#770303}.evidence-box .evidence:hover{background-color:rgba(170,170,170,0.5)}

.ghosts-box{color:white;width:1400px;margin:auto;margin-top:50px;display:flex;justify-content:space-between;align-items:center}.ghosts-box .ghosts-list{background-color:rgba(61,61,61,0.411);border:white 1px solid;display:flex;width:1000px;flex-direction:column}.ghosts-box .ghosts-list .list-row{display:flex}.ghosts-box .ghosts-list .list-row .ghost{width:200px;height:100px;display:flex;justify-content:center;align-items:center}.ghosts-box .ghosts-list .list-row .chosen{box-shadow:0px 0px 51px 4px rgba(0,0,0,0.31) inset;background-color:rgba(170,170,170,0.5)}.ghosts-box .ghosts-list .list-row .declined{background-color:#3e0303}.ghosts-box .ghosts-list .list-row .ghost:hover{background-color:rgba(170,170,170,0.5)}.ghosts-box .ghost-behavior{background-color:rgba(61,61,61,0.411);border:white 1px solid;overflow-y:scroll;width:300px;height:400px}.ghosts-box .ghost-behavior p{padding:5px}.ghosts-box .ghost-evidence{width:80px;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;height:400px}.ghosts-box .ghost-evidence img{width:40px}

.explicit-evidences-box{margin:auto;margin-top:50px;width:1400px;color:white;display:flex;align-items:center;justify-content:center;align-content:center}.explicit-evidences-box .explicit-evidence{display:flex;flex-direction:column;align-content:center;justify-content:center;align-items:center;border:solid white 2px;border-radius:20px;margin:0px 5px 0px 5px}.explicit-evidences-box .explicit-evidence p{width:200px;padding:5px;font-style:thin;font-weight:100;margin:5px}.explicit-evidences-box .explicit-evidence h3{padding:10px;font-weight:500;margin:5px}

@media (max-width: 991px){header h1{font-size:28px}.evidence-box{flex-direction:column;width:100px}.ghosts-box{flex-direction:column;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:10px}.ghosts-box .ghosts-list{width:300px}.ghosts-box .ghost-evidence{flex-direction:row;height:70px;width:200px}.explicit-evidences-box{width:300px;flex-direction:column}.explicit-evidences-box .explicit-evidence{margin:5px 5px 5px 5px}}

